* {
    text-decoration: none;
    list-style: none;
}

/* ------------------- MENÚ HAMBURGUESA ------------------- */
/* --- CONTENEDOR GENERAL --- */
.offcanvas.offcanvas-start {
    top: 0;
    left: 0;
    width: 100%;
    border-right: 0px;
    transform: translateX(-100%);
}

/* --- HAMBURGUESA --- */
.btn {
    background-color: transparent;
    border: 0px;
    --bs-btn-focus-box-shadow: 0 0 0 0.25rem rgba((255, 255, 255));

    &:hover {
        background-color: transparent;
        border: 0px;
    }
}

.btn-check:focus+.btn, .btn:focus {
    background-color: transparent;
    border: transparent;
    box-shadow: 0px transparent;
}

/* --- BOTÓN X --- */
.btn-close {
    background-color: transparent;
    border: 0px;
    --bs-btn-focus-box-shadow: 0 0 0 0.25rem rgba((255, 255, 255));
}

